@font-face {
  font-family: 'Avenir light';
  src: url('./Avenir\ Light.ttf');
}

.App {
  align-items: center;
  display: flex;
  font-family: 'Avenir light';
  flex-direction: column;
  letter-spacing: 1px;
  min-height: 100vh;
  height: 100%;
  justify-content: flex-start;
  line-height: 1.2rem;
  text-align: center;
}

main {
  align-items: flex-start;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

h1 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
  margin: 0 0 1rem 0;
}

.UserInput, 
.Insights {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 75vh;
  justify-content: flex-start;
  margin: 3rem;
}

.Insights > article{
  align-items: flex-start;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 3px;
  display: flex;
  flex-direction: column;
  height: 75vh;
  justify-content: flex-start;
  margin: 1rem 3rem;
  max-width: 312px;
  min-width: 200px;
  padding: 0.5rem;
  width: 100%;
}

.CounterTable, 
.LetterDensityTable,
.SocialMediaTable {
  align-items: flex-start;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-width: 200px;
  padding: 0.5rem;
  width: 100%;
}

