label {
  font-size: 2rem;
  font-weight: bold;
  margin: 0 0 1rem 0;
}

textarea {
  border: none;
  box-shadow: 0 0 5px #bac4ab;
  font-family: 'Avenir light', 'Courier New', Courier, monospace;
  height: 100%;
  letter-spacing: 1px;
  line-height: 1.2rem;
  margin: 1rem 0;
  min-width: 400px;
  padding: 0.75rem;
  width: 100%;
}

textarea:focus {
  box-shadow: 0 0 21px #bac4ab;
  outline: solid 2px #d0dbbf;
}

table {
  line-height: 1.2rem;
}

tbody {
  width: 100%;
}

tr {
  border-top: 1px solid rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: space-between;
}

tr:first-child {
  border-top: none;
}

tr > td:first-child {
  font-weight: 500;
}

h4 {
  margin: 0 auto 0.25rem;
}

hr {
  width: 100%;
}

/* LARGE */
@media only screen and (max-width: 992px) {
  h1 {
    margin: 2rem;
  }
  .UserInput, 
  .Insights {
    margin: 3rem 1rem;
    /* min-width: 60vw; */
  }
}

@media only screen and (max-width: 767px) {
  .UserInput {
    margin: 0.75rem auto;
    height: 30vh;
    min-width: 60vw;
  }

  .Insights {
    margin: 0.75rem auto;
    min-width: 60vw;
  }

  main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  textarea {
    min-width: 60vw;
    height: 100%;
    margin: 1rem 0;
    padding: 0.75rem;
    border: none;
    box-shadow: 0 0 5px #bac4ab;
  }
}